import Vue from 'vue'
import Router from 'vue-router'
import initial from '@/components/public/initial'
import work from '@/components/work/work'
import daysWea from '@/components/work/daysWea'
import person from '@/components/person/person'

Vue.use(Router)
const routes = [
  {
    path: '',
    redirect: {name: 'initial'}
  },
  {
    name: 'initial',
    path: '/initial',
    components: {
      default: initial,
      mainView: initial
    }
  },
  {
    name: 'work',
    path: ':city/work',
    components: {
      default: work,
      mainView: work
    },
    children: [
      {
        name: 'daysWea',
        path: 'daysWea',
        component: daysWea
      }
    ]
  },
  {
    path: '/person',
    name: 'person',
    components: {
      default: person,
      mainView: person
    }
  }
]

const router = new Router({routes})

export default router



